import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';
import { isNullOrUndefined } from 'util';
import { DipVmSettingModel } from './model/vmsetting.model';

export function CheckVmSettingListRepeat(vmValueList: DipVmSettingModel[]): boolean {
    let vmid = null;
    if (vmValueList != null && vmValueList.length > 0) {
        vmid = vmValueList[0].vmid;
        if (isNullOrUndefined(vmid) || isNullOrEmptyString(vmid)) {
            alert('值映射设置信息的值映射定义内码为空!');
            return false;
        }
    }

    for (const key in vmValueList) {
        if (vmValueList.hasOwnProperty(key)) {
            const element = vmValueList[key];
            const count = vmValueList.filter(vms =>
                CompareSrccol(element, vms)
            ).length;
            if (count > 1) {
                alert('值映射设置源列重复!');
                return false;
            }
        }
    }
    return true;
}


function CompareSrccol(newvms: DipVmSettingModel, vms: DipVmSettingModel) {

    if (newvms.vmid !== vms.vmid) {
        return false;
    }
    if (newvms.srccol01 !== vms.srccol01) {
        return false;
    }
    if (newvms.srccol02 !== vms.srccol02) {
        return false;
    }
    if (newvms.srccol03 !== vms.srccol03) {
        return false;
    }
    if (newvms.srccol04 !== vms.srccol04) {
        return false;
    }
    if (newvms.srccol05 !== vms.srccol05) {
        return false;
    }
    if (newvms.srccol06 !== vms.srccol06) {
        return false;
    }
    if (newvms.srccol07 !== vms.srccol07) {
        return false;
    }
    if (newvms.srccol08 !== vms.srccol08) {
        return false;
    }
    if (newvms.srccol09 !== vms.srccol09) {
        return false;
    }
    if (newvms.srccol10 !== vms.srccol10) {
        return false;
    }
    if (newvms.srccolexpression !== vms.srccolexpression) {
        return false;
    }
    return true;
}
export class VmSettingValidator {

}
