import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { ValueMapping } from '../model/valuemapping.model';

const url = '/api/dev/dip/v1.0/valuemapping/'; // http://localhost:5000
const getDipValueMappingList = 'dipValueMappingList';

@Injectable()
export class DipValueMappingModelService {
    constructor(private http: HttpClient) {
    }
    // 获取值映射定义信息
    getDipValueMappingModelList(): Observable<ValueMapping[]> {
        return this.http.get<ValueMapping[]>(url + getDipValueMappingList);
    }
    // Guid生成
    getGuid(): Observable<string> {
        return this.http.get<string>(url + 'guid');
    }

}
