﻿import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaderResponse, HttpHeaders, HttpParams } from '@angular/common/http';
import { DipValueMappingModel } from '../model/DipValueMappingModel';
import { Observable } from 'rxjs/Observable';
import { isNullOrUndefined } from 'util';
import { tap, catchError } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';
import { DipVmColumnModel } from '../model/DipVmColumnModel';
import { ValueMappingEditModel } from '../model/ValueMappingEditModel';

const CREATE_ACTION = 'dipValueMapping';
const UPDATE_ACTION = 'dipValueMapping';
const REMOVE_ACTION = 'dipValueMapping';
const RESET_ACTION = 'dipValueMapping';
const START_ACTION = 'dipValueMappingList';
 // const url = '/api/runtime/dip/v1.0/valuemapping/';
 // http://localhost:5000
const url = '/api/dev/dip/v1.0/valuemapping/';
@Injectable()
export class DipValueMappingModelService {
  // 从指定的地址拉取数据


  private vmList: DipValueMappingModel[];
  private vmColumnAddList: DipVmColumnModel[];
  private vmColumnEditList: DipVmColumnModel[];
  private vmColumnDeleteIds: string[];
  public valueMappingEditModel: ValueMappingEditModel = new ValueMappingEditModel;
  private selectDipVmModel: DipValueMappingModel;

  // private headers = new HttpHeaders({
  //  'Content-Type': 'application/json',
  //  'Authorization': 'my-auth-token'
  // });
  getselectDipVmModel(): DipValueMappingModel {
    return this.selectDipVmModel;
  }
  constructor(private http: HttpClient) {

  }
  getGuid(): Observable<string> {
    return this.http.get<string>(url + 'guid');
  }
  getDipValueMappingModelList(): Observable<DipValueMappingModel[]> {
    return this.http.get<DipValueMappingModel[]>(url + START_ACTION)
      .pipe(
        tap(vmlist => this.vmList = vmlist),
        catchError(this.handleError<DipValueMappingModel[]>('getDipValueMappingModelList'))
      );
  }
  addDipValueMappingModel(valueMappingModel: DipValueMappingModel): Observable<DipValueMappingModel> {
    return this.http.post<DipValueMappingModel>(url + CREATE_ACTION, valueMappingModel)
      .pipe(
        tap(vmmodel => console.log('增加', vmmodel)),
        catchError(this.handleError<DipValueMappingModel>('addDipValueMappingModel'))
      );
  }
  updateDipValueMappingModel(vmEditModel: ValueMappingEditModel): Observable<any> {
    return this.http.put(url + UPDATE_ACTION, vmEditModel)
      .pipe(
        tap(_ => console.log('更新值映射')),
        catchError(this.handleError<any>('updateDipValueMappingModel'))
      );
  }


  /** DELETE: delete the DipValueMappingModel from the server */
  deleteDipValueMappingModel(vmid: string): Observable<{}> {
    // TODO vmid != null
    const deleteUrl = url + REMOVE_ACTION;
    return this.http.delete(deleteUrl + '/' + vmid);
  }
  getDipValueMappingModel(id: string): Observable<DipValueMappingModel> {
     // TODO vmid != null
    const getUrl = url + RESET_ACTION;
    return this.http.get<DipValueMappingModel>(getUrl + '/' + id);
  }

  /**
 * Handle Http operation that failed.
 * Let the app continue.
 * @param operation - name of the operation that failed
 * @param result - optional value to return as the observable result
 */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
