export class DipVmColumnModel {
  public id: string;
  public vmId: string;
  public code: string;
  public colText: string;
  public dataType: number = 0;
  public editMode: number = 0;
  public helpId: string;
  public direction: number = 0;
  public helpColumn: number = 0;
  public helpFilter: string;
  public groupFilter: string;
}
