import { GridComponent } from '@progress/kendo-angular-grid';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import {isNullOrUndefined} from 'util';
import {FormBuilder, FormGroup} from '@angular/forms';
import { DipVmColumnModel } from '../model/DipVmColumnModel';
import { DipValueMappingModelService } from '../service/dipvaluemappingservice.service';

@Component({
  selector: 'vm-tar-grid',
  templateUrl: `vm-tar-column.component.html`
})
export class VmTarColumnComponent implements OnInit {
  @ViewChild('tarGrid')
 public tarGrid: GridComponent;
  public mySelection: number[] = [0];
  public gridData: DipVmColumnModel[];
  private _tarValueMappingColumns: DipVmColumnModel[];

  @Input()
  set tarValueMappingColumns(tarValueMappingColumns: DipVmColumnModel[]) {
    this._tarValueMappingColumns = tarValueMappingColumns;
    console.log('tar----' + this._tarValueMappingColumns);
    this.gridData = this.tarValueMappingColumns;
    // this.getValueMappingSrcColumnList();
  }

  get tarValueMappingColumns(): DipVmColumnModel[] {
    return this._tarValueMappingColumns;
  }

  constructor(private formBuilder: FormBuilder, private dipValueMappingService: DipValueMappingModelService ) {

  }

  ngOnInit() {

  }
  public cellClickHandler({sender, rowIndex, columnIndex, dataItem, isEdited}) {
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
    }
  }
// 单元格失去焦点
  public cellCloseHandler(args: any) {
    const {formGroup, dataItem} = args;
    console.log(dataItem);
    console.log(formGroup.value);
    Object.assign(dataItem, formGroup.value);
    this.gridData[0] = formGroup.value as DipVmColumnModel;
    console.log(formGroup.value);
    const index = this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList
      .findIndex(vmc => vmc.id === (formGroup.value as DipVmColumnModel).id);
    if (index > -1) {
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList.splice(index, 1);
    }
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList.push(formGroup.value as DipVmColumnModel);
    // this.tarGrid.closeCell();
   // this.tarGrid.cancelCell();
  }

  public createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': dataItem.id,
      'vmId': dataItem.vmId,
      'code': dataItem.code,
      'colText': dataItem.colText,
      'dataType': dataItem.dataType,
      'editMode': dataItem.editMode,
      'helpId': dataItem.helpId,
      'helpCode': dataItem.helpCode,
      'direction': dataItem.direction,
      'helpColumn': dataItem.helpColumn,
      'helpFilter': dataItem.helpFilter,
      'groupFilter': dataItem.groupFilter
    });
  }
}
