import { VmTarColumnComponent } from './../vmcolumn/vm-tar-column.component';
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { StateControllorService } from '../Service/stateControllor.service';
import { DipValueMappingModel } from '../model/DipValueMappingModel';
import { DipVmColumnModel } from '../model/DipVmColumnModel';
import { VmSrcColumnComponent } from '../vmcolumn/vm-src-column.component';
import { DipValueMappingModelService } from '../service/dipvaluemappingservice.service';
import { isNullOrUndefined } from 'util';


@Component({
  selector: 'valueMapping-baseInfo',
  templateUrl: `./valueMapping-baseInfo.component.html`
})

export class ValueMappingBaseInfoComponent implements OnInit {
  @ViewChild(VmSrcColumnComponent)
  private srcVmColGridComponent: VmSrcColumnComponent;
  @ViewChild(VmTarColumnComponent)
  private tarVmColGridComponent: VmTarColumnComponent;
  public tarVmcs: DipVmColumnModel[];
  public srcVmcs: DipVmColumnModel[];
  // public vmid: string;
  private _valueMapping = new DipValueMappingModel;

  @Input()
  set valueMapping(valueMappinng: DipValueMappingModel) {
    console.log(111);
    if (isNullOrUndefined(valueMappinng) === true) {
      valueMappinng = new DipValueMappingModel;
    }
    this._valueMapping = valueMappinng;
    this.srcVmcs = valueMappinng.srcVmColumnModelList;
    this.tarVmcs = [valueMappinng.tarVmColumnModel];
    console.log('this.ValueMapping:', this.valueMapping);
  }

  get valueMapping(): DipValueMappingModel {
    return this._valueMapping;
  }

  constructor(private stateControllorService: StateControllorService, private dipValueMappingService: DipValueMappingModelService) {
  }

  ngOnInit() {

  }
  getState(): boolean {
    const state = this.stateControllorService.getState();
    return state;
  }

  addVmColumn() {
    this.dipValueMappingService.getGuid().subscribe(
      guid => {
        const srcVmColumn = new DipVmColumnModel();
        srcVmColumn.vmId = this.valueMapping.id;
        srcVmColumn.id = guid;
        const code = this.getSrcColCode();
        if (isNullOrUndefined(code) === false) {
          srcVmColumn.code = code;
          srcVmColumn.direction = 1;
          srcVmColumn.editMode = 0;
          srcVmColumn.dataType = 0;
          srcVmColumn.helpColumn = 0;
          this.srcVmColGridComponent.addVmColumn(srcVmColumn);
        } else {
          alert('值映射源列最多10列');
        }
      });
  }

  removeVmColumn() {
    this.srcVmColGridComponent.removeVmColumn();
  }

  closeEditor() {
    this.srcVmColGridComponent.kSrcGrid.closeCell();
    this.srcVmColGridComponent.kSrcGrid.cancelCell();
    this.tarVmColGridComponent.tarGrid.closeCell();
    this.tarVmColGridComponent.tarGrid.cancelCell();
  }

  getSrcColCode(): string {
    let code = 'SRCCOL';
    // let num = 1;
    for (let index = 1; index <= 10; index++) {
      const len = index.toString().length;
      if (len < 2) {
        code += '0' + index.toString();
      } else {
        code += index.toString();
      }
      if (this.isCantainedSrccode(code) === true) {
        code = 'SRCCOL';
      } else {
        return code;
      }
    }
    return null;
  }

  isCantainedSrccode(code: string): boolean {
    let index = this.srcVmColGridComponent.gridData.findIndex(vmc => vmc.code === code);
    if (index > -1) {
      return true;
    }
    index = this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList
      .findIndex(vmc => vmc.code === code);
    if (index > -1) {
      return true;
    }
    return false;
  }
}

