import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { VmsettingComponent } from './vmsetting/vmsetting.component';
import { ValueMappingGridComponent } from './valuemapping-definition/valueMapping-grid.component';

const routes: Routes = [
  { path: '', redirectTo: '/valuemapping', pathMatch: 'full' },
  { path: 'vmsetting', component: VmsettingComponent },
  { path: 'valuemapping', component: ValueMappingGridComponent }

];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }
