import { Component, EventEmitter, Output, OnInit, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

import { Http } from '@angular/http';
import { isNullOrUndefined } from 'util';
import { StateControllorService } from './Service/stateControllor.service';
import { DipValueMappingModelService } from './service/dipvaluemappingservice.service';
import { DipValueMappingModel } from './model/DipValueMappingModel';
import { DipVmColumnModel } from './model/DipVmColumnModel';
import { ValueMappingBaseInfoComponent } from './baseinfo/valueMapping-baseInfo.component';
import { ValueMappingEditModel } from './model/ValueMappingEditModel';
import { CheckValueMapping, CheckVmColumnListRepeat } from './validator/valuemapping-validator';
import { CheckDipVmColumn, CheckValueMappingListRepeat } from './validator/valuemapping-validator';

@Component({
  selector: 'valueMapping-grid',
  templateUrl: `./valueMapping-grid.component.html`,
  styleUrls: ['./valueMapping-grid.component.css'],
  providers: [DipValueMappingModelService, StateControllorService]
})
export class ValueMappingGridComponent implements OnInit, AfterViewInit {
  @ViewChild('grid')
  kGrid: GridComponent;
  @ViewChild(ValueMappingBaseInfoComponent)
  private vmBaseinfoComponent: ValueMappingBaseInfoComponent;
  public btnState = true;
  public data: DipValueMappingModel[];
  public mySelection: number[] = [0];
  public editedRowIndex: number = 0;

  selectedValueMapping: DipValueMappingModel;
  private isNew: boolean;

  ngAfterViewInit() {
    console.log(this.kGrid);
  }

  constructor(private dipValueMappingService: DipValueMappingModelService,
    private stateControllorService: StateControllorService) {
  }

  ngOnInit(): void {
    this.getValueMappingData();
  }

  changeDisabled() {
    this.btnState = this.stateControllorService.changeState();
  }
  onSelectedKeysChange(e) {
    console.log(this.data);
    console.log('mySelection:' + this.mySelection);
    console.log('e:' + e);
    let vm1 = this.kGrid.data[e] as DipValueMappingModel;
    this.dipValueMappingService.getDipValueMappingModel(vm1.id).subscribe(vmModel => {
      vm1 = vmModel;
      this.selectedValueMapping = vm1;
      console.log('vm1', vm1);
    });
    console.log(vm1);
  }

  getValueMappingData(): void {
    this.dipValueMappingService.getDipValueMappingModelList()
      .subscribe(vmlist => {
        this.data = vmlist as DipValueMappingModel[];
        if (vmlist.length === 0) {
          this.mySelection = [];
        } else {
          this.mySelection = [0];
          console.log('this.data', this.data);
          this.getValueMapping(this.data[0].id);
        }
      });
  }

  getValueMapping(vmid: string) {
    this.dipValueMappingService.getDipValueMappingModel(vmid)
      .subscribe(vm => {
        this.selectedValueMapping = vm;
      });
  }

  saveValueMapping(): void {
    this.vmBaseinfoComponent.closeEditor();
    console.log('saveValueMapping', this.selectedValueMapping);
    console.log('this.isNew', this.isNew);
    if (this.checkSelectValueMapping() === false) {
      this.getValueMapping(this.selectedValueMapping.id);
    } else {
      if (this.isNew === true) {
        this.dipValueMappingService.addDipValueMappingModel(this.selectedValueMapping)
          .subscribe(
            vm => {
              console.log(vm);
              this.selectedValueMapping = vm;
              console.log(this.mySelection);
              const rowIndex = this.mySelection[0];
              this.kGrid.closeRow(rowIndex);
              // this.getValueMappingData();
              console.log(this.kGrid.data[rowIndex]);
            }
          );
      } else {
        console.log('2222222222', this.dipValueMappingService.valueMappingEditModel);
        this.dipValueMappingService.updateDipValueMappingModel(this.dipValueMappingService.valueMappingEditModel).subscribe(
          vm => {
            this.dipValueMappingService.valueMappingEditModel = new ValueMappingEditModel;
            console.log(this.mySelection);
            const rowIndex = this.mySelection[0];
            this.kGrid.closeRow(rowIndex);
           // this.getValueMappingData();
            console.log(this.kGrid.data[rowIndex]);
          }
        );
      }
    }
    this.dipValueMappingService.valueMappingEditModel = new ValueMappingEditModel;
  }

  checkSelectValueMapping(): boolean {
    if (CheckValueMapping(this.selectedValueMapping) === false) {
      return false;
    }
    if (isNullOrUndefined(this.selectedValueMapping.srcVmColumnModelList) || this.selectedValueMapping.srcVmColumnModelList.length === 0) {
      alert('值映射列源列至少有一列');
      return false;
    }
    if (CheckValueMappingListRepeat(this.data) === false) {
      return false;
    }
    if (CheckDipVmColumn(this.selectedValueMapping.tarVmColumnModel) === false) {
      return false;
    }
    if (CheckVmColumnListRepeat(this.selectedValueMapping.srcVmColumnModelList) === false) {
      return false;
    }
    return true;
  }
  addHandler() {
    this.changeDisabled();
    this.dipValueMappingService.getGuid().subscribe(guid => {
      this.dipValueMappingService.getGuid().subscribe(targuid => {
        this.dipValueMappingService.getGuid().subscribe(srcguid => {
          console.log('addHandler');
          this.isNew = true;
          console.log('sender:', this.kGrid);
          const vm = this.getAddValueMapping(guid);
          const tarVmColumn = this.getTarVmColumn(targuid);
          tarVmColumn.vmId = guid;
          vm.tarVmColumnModel = tarVmColumn;
          const srcVmColumn = this.getSrcVmColumn(srcguid);
          srcVmColumn.vmId = guid;
          vm.srcVmColumnModelList = [srcVmColumn];
          this.selectedValueMapping = vm;
          const arr = [vm];
          this.data = arr.concat(this.data);
          console.log(this.mySelection, '****', this.data);
          console.log('addHandler');
        });
      });
    });
  }

  getAddValueMapping(id: string): DipValueMappingModel {
    const vm = new DipValueMappingModel();
    vm.id = id;
    console.log('editedRowIndex', this.editedRowIndex);
    this.closeEditor(this.kGrid);
    console.log(vm);
    this.kGrid.addRow(vm);
    console.log(vm);
    return vm;
  }

  getTarVmColumn(id: string): DipVmColumnModel {
    const tarVmColumn = new DipVmColumnModel();
    tarVmColumn.id = id;
    tarVmColumn.code = 'TARCOL';
    tarVmColumn.direction = 2;
    tarVmColumn.editMode = 0;
    tarVmColumn.dataType = 0;
    tarVmColumn.helpColumn = 0;
    return tarVmColumn;
  }
  getSrcVmColumn(id: string): DipVmColumnModel {
    const srcVmColumn = new DipVmColumnModel();
    srcVmColumn.id = id;
    srcVmColumn.code = 'SRCCOL01';
    srcVmColumn.direction = 1;
    srcVmColumn.editMode = 0;
    srcVmColumn.dataType = 0;
    srcVmColumn.helpColumn = 0;
    return srcVmColumn;
  }

  editHandler() {
    this.changeDisabled();
    console.log('editHandler');
    this.isNew = false;
    console.log('myselection', this.mySelection);
    console.log('selectedMapping', this.selectedValueMapping);
    this.editedRowIndex = this.mySelection[0];
    this.dipValueMappingService.getDipValueMappingModel(this.selectedValueMapping.id)
      .subscribe(model => {
        this.selectedValueMapping = model;
        // TODO 值映射定义编辑处理
        this.dipValueMappingService.valueMappingEditModel.valueMappingModel = this.selectedValueMapping;
      });
    console.log('editHandler');
  }

  public cancelHandler() {
    this.changeDisabled();
    this.kGrid.cancelCell();
    this.kGrid.closeRow();
    console.log(this.selectedValueMapping);
    if (this.isNew === true) {
      this.data.splice(0, 1);
      if (this.data.length > 0) {
        this.mySelection = [0];
        this.getValueMapping(this.data[0].id);
      } else {
        this.mySelection = [];
        this.selectedValueMapping = new DipValueMappingModel;
      }
    } else {
      if (isNullOrUndefined(this.selectedValueMapping) === false && isNullOrUndefined(this.selectedValueMapping.id) === false) {
        this.getValueMapping(this.selectedValueMapping.id);
      }
    }

    console.log('cancelHandler');
    this.dipValueMappingService.valueMappingEditModel = new ValueMappingEditModel;
  }

  saveHandler() {
    this.changeDisabled();
    console.log('saveHandler');
    this.saveValueMapping();
  }

  removeHandler() {
    console.log('remove');
    console.log(this.selectedValueMapping);
    // console.log('dataitem', dataItem);
    this.deleteValueMapping(this.selectedValueMapping.id);
  }

  private deleteValueMapping(id: string) {
    this.dipValueMappingService.deleteDipValueMappingModel(id)
      .subscribe(value => {
        console.log(value);
        this.getValueMappingData();
      });
  }

  private closeEditor(grid, rowIndex = this.editedRowIndex) {
    grid.closeRow(rowIndex);
    // this.editService.resetItem(this.editedProduct);
    const vm = this.selectedValueMapping;
    console.log('vm', vm);
    this.editedRowIndex = undefined;
  }

  getDisabledState() {
    return this.stateControllorService.getState();
  }

}
