import { VmSettingEditModel } from './../model/vmsetting-editmodel.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { DipVmSettingModel } from '../model/vmsetting.model';

const url = '/api/dev/dip/v1.0/valuemapping/setting/';
 // 'http://localhost:5000';
// 获取值映射设置列表getVmValueLists/vmid
const getVmValueLists = 'vmValueList/';
// 获取值映射设置列名 getVmSettingColName/vmid
const getVmSettingColName = 'vmSettingColName/';
// 更新值映射设置信息 body
const updateVmSetting = 'vmSetting';
@Injectable()
export class DipVmSettingModelService {

    public vmsettingEditModel: VmSettingEditModel = new VmSettingEditModel();
    public vmSettingData: DipVmSettingModel[] = new Array<DipVmSettingModel>();
    constructor(private http: HttpClient) {
    }
    // 获取值映射设置信息
    getVmSettingList(vmid: string): Observable<DipVmSettingModel[]> {
         // TODO vmid != null
        return this.http.get<DipVmSettingModel[]>(url + getVmValueLists + vmid);
    }
    // 获取值映射设置列名
    getVmSettingColName(vmid: string): Observable<any[]> {
         // TODO vmid != null
        return this.http.get<any[]>(url + getVmSettingColName + vmid);
    }
    // 更新值映射设置信息
    updateVmSetting(vmSettingEditModel: VmSettingEditModel): Observable<any> {
        console.log('updateVmSetting', vmSettingEditModel);
        return this.http.put(url + updateVmSetting, vmSettingEditModel);
    }
}
