import { VmSettingValidator, CheckVmSettingListRepeat } from './vmsetting-validator';
import { VmSettingEditModel } from './model/vmsetting-editmodel.model';
import { GridComponent, RowClassArgs } from '@progress/kendo-angular-grid';
import { Component, OnInit, Injector, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ValueMapping } from './model/valuemapping.model';
import { DipVmSettingModel } from './model/vmsetting.model';
import { DipValueMappingModelService } from './service/valuemapping.service';
import { DipVmSettingModelService } from './service/vmsetting.service';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';

@Component({
  selector: 'app-vmsetting',
  templateUrl: './vmsetting.component.html',
  styleUrls: ['./vmsetting.component.css'],
  providers: [DipValueMappingModelService, DipVmSettingModelService]
})
export class VmsettingComponent implements OnInit {
  @ViewChild('vmsGrid')
  vmsGrid: GridComponent;

  vmdata: ValueMapping[];
  selectedValueMapping: ValueMapping;
  mySelection: number[] = [];
  vmsettingSelection: number[] = [];
  vmsettingdata: DipVmSettingModel[];
  colnameList: any;
  vmdata1: any;

  constructor(private httpClient: HttpClient,
    private formBuilder: FormBuilder,
    private vmservice: DipValueMappingModelService,
    private vmsettingservice: DipVmSettingModelService
  ) {

  }
  ngOnInit() {
    console.log('loadvm开始');
    // 界面初始化
    this.getValueMappings();
  }

  // 单元格点击编辑
  public cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
    }
  }
  // 单元格编译失去焦点
  public cellCloseHandler(args: any) {
    const { formGroup, dataItem } = args;
    // console.log('formGroup', formGroup);
    // console.log('dataItem', dataItem);
    console.log(this.vmsettingSelection);
    // 增加行处理
    const index = this.vmsettingservice.vmsettingEditModel.vmSettingAddList
      .findIndex(vmc1 => vmc1.id === dataItem.id);
    if (index > -1) {
      const addVmSetting = formGroup.value as DipVmSettingModel;
      Object.assign(dataItem, addVmSetting);
      addVmSetting.vmid = this.selectedValueMapping.id;
      this.vmsettingservice.vmsettingEditModel.vmSettingAddList.splice(index, 1);
      this.vmsettingservice.vmsettingEditModel.vmSettingAddList.push(addVmSetting);
    } else {
      // 编辑行处理
      if (!formGroup.valid) {
        args.preventDefault();
      } else if (formGroup.dirty) {
        console.log(dataItem);
        const editVmSetting = formGroup.value as DipVmSettingModel;
        Object.assign(dataItem, editVmSetting);
        console.log('editVmSetting' + editVmSetting);
        const editindex = this.vmsettingservice.vmsettingEditModel.vmSettingEditList
          .findIndex(vmsetting => vmsetting.id === editVmSetting.id);
        if (editindex > -1) {
          this.vmsettingservice.vmsettingEditModel.vmSettingEditList.splice(editindex, 1);
        }
        this.vmsettingservice.vmsettingEditModel.vmSettingEditList.push(editVmSetting);
      }
    }
    // 保存值映射设置数据
    const num = this.vmsettingdata.findIndex(vmsetting => vmsetting.id === dataItem.id);
    this.vmsGrid.data[num] = dataItem;
    console.log('cellCloseHandler', this.vmsettingSelection);
  }

  // 值映射定义切换事件
  onSelectedKeysChange(e) {
    console.log(this.vmdata);
    console.log('mySelection:' + this.mySelection);
    console.log('e:' + e);
    const vm = this.vmdata[e];
    this.selectedValueMapping = vm;
    console.log(vm);
    this.vmsettingSelection = [];
    this.vmsettingservice.vmsettingEditModel = new VmSettingEditModel;
    this.getVmSettingColName(vm.id);
    this.getVmSettingList(vm.id);
  }

  // 增加行
  addVmSettingHandler() {
    this.vmservice.getGuid()
      .subscribe(guid => {
        const vmsetting = new DipVmSettingModel();
        vmsetting.id = guid;
        vmsetting.vmid = this.selectedValueMapping.id;
        console.log(vmsetting);
        this.addVmSetting(vmsetting);
      });
  }
  // 删除行
  removeVmSettingHandler() {
    console.log(this.vmsettingSelection);
    if (this.vmsettingdata != null && this.vmsettingdata.length > 0) {
      const selectVmsIndex = this.vmsettingSelection[0];
      const vms = this.vmsettingdata[selectVmsIndex];
      const addindex = this.vmsettingservice.vmsettingEditModel.vmSettingAddList
        .findIndex(vmc1 => vmc1.id === vms.id);
      if (addindex > -1) {
        this.vmsettingservice.vmsettingEditModel.vmSettingAddList.splice(addindex, 1);
      } else {
        this.vmsettingservice.vmsettingEditModel.vmSettingDeleteList.push(vms.id);
      }
      const editIndex = this.vmsettingservice.vmsettingEditModel.vmSettingEditList
        .findIndex(vmc1 => vmc1.id === vms.id);
      if (editIndex > -1) {
        this.vmsettingservice.vmsettingEditModel.vmSettingEditList.splice(editIndex, 1);
      }
      this.vmsettingdata.splice(selectVmsIndex, 1);
      if (this.vmsettingdata.length === selectVmsIndex) {
        this.vmsettingSelection = [selectVmsIndex - 1];
      }
    } else {
      alert('无删除数据');
    }

  }
  // 保存值映射设置
  saveVmSettingHandler() {
    this.vmsGrid.closeCell();
    this.vmsGrid.cancelCell();
    const checkResult = this.checkVmSettingData();
    if (checkResult === false) {
      this.getVmSettingList(this.selectedValueMapping.id);
      return;
    } else {
      const isRepeat = CheckVmSettingListRepeat(this.vmsettingdata);
      console.log('isrepeat', isRepeat);
      if (isRepeat === false) {
        this.getVmSettingList(this.selectedValueMapping.id);
        return;
      }
      console.log('saveVmSettingHandler', this.vmsettingservice.vmsettingEditModel);
      this.vmsettingservice.updateVmSetting(this.vmsettingservice.vmsettingEditModel)
        .subscribe(() => {
          this.vmsettingservice.vmsettingEditModel = new VmSettingEditModel();
          // this.getValueMappings();
          alert('保存成功');
        });
    }

  }
  // 取消编辑值映射设置
  cancelHandler() {
    this.vmsGrid.cancelCell();
    this.vmsGrid.closeRow();
    console.log(this.vmsettingdata);
    console.log(this.selectedValueMapping);
    if (isNullOrUndefined(this.selectedValueMapping) === false && isNullOrUndefined(this.selectedValueMapping.id) === false) {
      this.getVmSettingList(this.selectedValueMapping.id);
    }
  }
  // 单双行颜色
  public rowCallback(context: RowClassArgs) {
    const isEven = context.index % 2 === 0;
    return {
      even: isEven,
      odd: !isEven
    };
  }
  /*
  **方法
  */
  // 获取值映射定义列表信息
  getValueMappings() {
    console.log();
    this.vmservice.getDipValueMappingModelList()
      .subscribe(context => {
        console.log('valuemappingdata', context);
        this.vmdata = context as ValueMapping[];
        if (context.length > 0) {
          console.log(context[0]);
          this.selectedValueMapping = context[0];
          this.mySelection = [0];
          this.getVmSettingColName(context[0].id);
          this.getVmSettingList(context[0].id);
        } else {
          this.selectedValueMapping = undefined;
          this.mySelection = [];
          // TODO 隐藏工具栏和grid 不可用
        }
      });
  }
  // 获取值映射设置列名
  getVmSettingColName(vmid: string) {
    this.vmsettingservice.getVmSettingColName(vmid)
      .subscribe(context => {
        console.log('dict', context);
        this.colnameList = context;
      });
  }
  // 获取值映射设置信息
  getVmSettingList(vmid: string) {
    this.vmsettingservice.getVmSettingList(vmid)
      .subscribe(context => {
        console.log('vmsettinglist', context);
        this.vmsettingdata = context;
        this.vmsettingservice.vmSettingData = context;
        if (context != null && context.length > 0) {
          if (this.vmsettingSelection.length > 0) {
            const index = this.vmsettingSelection[0];
            this.vmsettingSelection = [index];
          } else {
            this.vmsettingSelection = [0];
          }

        }
        console.log(this.vmsettingservice.vmSettingData);
      });
  }

  // 增加值映射设置数据
  addVmSetting(vmsetting: DipVmSettingModel) {
    console.log('addVmcolumn');
    console.log(vmsetting);
    const index = this.vmsettingservice.vmsettingEditModel.vmSettingAddList
      .findIndex(vmc1 => vmc1.id === vmsetting.id);
    if (index > -1) {
      this.vmsettingservice.vmsettingEditModel.vmSettingAddList.splice(index, 1);
    } else {
      this.vmsettingservice.vmsettingEditModel.vmSettingAddList.push(vmsetting);
    }
    console.log(this.vmsettingservice.vmSettingData);
    this.vmsettingdata.push(vmsetting);
    this.vmsettingSelection = [this.vmsettingdata.length - 1];
    console.log('vmsettingdata', this.vmsettingdata);
    console.log('addVmcolumn');
    console.log(this.vmsettingservice.vmSettingData);
  }

  // 值映射设置表单信息
  public createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': dataItem.id,
      'vmid': dataItem.vmid,
      'srccol01': dataItem.srccol01,
      'srccol02': dataItem.srccol02,
      'srccol03': dataItem.srccol03,
      'srccol04': dataItem.srccol04,
      'srccol05': dataItem.srccol05,
      'srccol06': dataItem.srccol06,
      'srccol07': dataItem.srccol07,
      'srccol08': dataItem.srccol08,
      'srccol09': dataItem.srccol09,
      'srccol10': dataItem.srccol10,
      'srccolexpression': dataItem.srccolexpression,
      'tarcol': dataItem.tarcol
    });
  }


  // 值映射设置空值校验
  checkVmSettingData(): boolean {
    let checkresult = true;
    for (let index = 0; index < this.vmsettingdata.length; index++) {
      const vmsetting = this.vmsettingdata[index];
      console.log(vmsetting);
      for (const key in this.colnameList) {
        if (this.colnameList.hasOwnProperty(key)) {
          const dict = this.colnameList[key];
          switch (dict.field) {
            case 'srccol01':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol01, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol02':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol02, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol03':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol03, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol04':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol04, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol05':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol05, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol06':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol06, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol07':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol07, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol08':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol08, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol09':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol09, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccol10':
              checkresult = this.checkVmSettingColumn(vmsetting.srccol10, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'srccolexpression':
              checkresult = this.checkVmSettingColumn(vmsetting.srccolexpression, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            case 'tarcol':
              checkresult = this.checkVmSettingColumn(vmsetting.tarcol, index, dict.title);
              if (checkresult === false) {
                return checkresult;
              }
              break;
            default:
              break;
          }
        }
      }
    }
    return checkresult;
  }
  // 值映射列空值校验
  checkVmSettingColumn(colvalue: string, index: number, title: string): boolean {
    if (isNullOrUndefined(colvalue) || isNullOrEmptyString(colvalue)) {
      alert('第' + (index + 1) + '行' + title + '为空!');
      return false;
    } else if (colvalue.length >= 50) {
      alert('第' + (index + 1) + '行' + title + '长度超过50!');
      return false;
    } else {
      return true;
    }
  }
}
