import { AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { isWhiteSpace } from 'tslint';
import { GridComponent } from '@progress/kendo-angular-grid';
import { noUndefined } from '@angular/compiler/src/util';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DipVmColumnModel } from '../model/DipVmColumnModel';
import { DipValueMappingModelService } from '../service/dipvaluemappingservice.service';

@Component({
  selector: 'vm-src-grid',
  templateUrl: `vm-src-column.component.html`
})
export class VmSrcColumnComponent implements OnInit, AfterViewInit {
  @ViewChild('srcGrid')
  kSrcGrid: GridComponent;
  public gridData: DipVmColumnModel[];
  public tempData: DipVmColumnModel[];
  public editedRowIndex: number = 0;
  public mySelection: number[] = [];
  selectedValueMappingColumn: DipVmColumnModel;
  private _srcValueMappingColumns: DipVmColumnModel[];
  @Input()
  set srcValueMappingColumns(srcValueMappingColumns: DipVmColumnModel[]) {
    this._srcValueMappingColumns = srcValueMappingColumns;
    console.log('src----', this._srcValueMappingColumns);
    this.gridData = this.srcValueMappingColumns;
    this.tempData = this.srcValueMappingColumns;
    if (isNullOrUndefined(this.gridData) === false && this.gridData.length > 0) {
      this.selectedValueMappingColumn = this.gridData[0];
      this.mySelection = [0];
    }
    // this.getValueMappingSrcColumnList();
  }

  get srcValueMappingColumns(): DipVmColumnModel[] {
    return this._srcValueMappingColumns;
  }
  @Input() vmid: string;

  constructor(private formBuilder: FormBuilder, private dipValueMappingService: DipValueMappingModelService) {
    // console.log('vmid==' + this.vmid);
  }

  ngOnInit() {
    // this.getValueMappingSrcColumnList();
  }

  ngAfterViewInit() {
    console.log(this.kSrcGrid);
  }

  getValueMappingSrcColumnList(): void {
    //  this.valueMappingColumnService.getDepValueMappingSrcColumnList(this.vmid).subscribe(value => this.gridData = value);
  }

  onSelectedKeysChange(e) {
    console.log(this.gridData);
    console.log('e:', e);
    const vmc1 = this.kSrcGrid.data[e];
    this.editedRowIndex = e;
    this.selectedValueMappingColumn = vmc1;
    console.log(vmc1);
  }

  addVmColumn(vmc: DipVmColumnModel) {
    console.log('addVmcolumn');
    console.log(vmc);
    // this.kSrcGrid.addRow(vmc);
    this.selectedValueMappingColumn = vmc;
    const index = this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList
      .findIndex(vmc1 => vmc1.id === vmc.id);
    if (index > -1) {
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList.splice(index, 1);
    } else {
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList.push(vmc);
    }
    console.log('addthis.selectedValueMappingColumn', this.selectedValueMappingColumn);
    // this.tempData.push(vmc);
    this.gridData.push(vmc);
    console.log(this.editedRowIndex);
    console.log('addVmcolumn');
  }

  removeVmColumn() {

    if (this.gridData !== null && this.gridData.length > 0) {
      const selectVmcIndex = this.mySelection[0];
      const vmc = this.gridData[selectVmcIndex];
      const addindex = this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList
        .findIndex(vmc1 => vmc1.id === vmc.id);
      if (addindex > -1) {
        this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList.splice(addindex, 1);
      } else {
        this.dipValueMappingService.valueMappingEditModel.deleteVmColumns.push(this.selectedValueMappingColumn.id);
      }
      const editIndex = this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList
        .findIndex(vmc1 => vmc1.id === vmc.id);
      if (editIndex > -1) {
        this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList.splice(editIndex, 1);
      }
      this.gridData.splice(selectVmcIndex, 1);
      if (this.gridData.length === selectVmcIndex) {
        this.mySelection = [selectVmcIndex - 1];
      }
    } else {
      alert('无删除数据');
    }
  }

  public cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
    if (columnIndex === 0) {
      return;
    }
    if (!isEdited) {
      const fromGroup = this.createFormGroup(dataItem);
      this.kSrcGrid.editCell(rowIndex, columnIndex, fromGroup);
    }
  }

  public cellCloseHandler(args: any) {
    const { formGroup, dataItem } = args;
    const index = this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList
      .findIndex(vmc1 => vmc1.id === dataItem.id);
    if (index > -1) {
      const addVmColumn = formGroup.value as DipVmColumnModel;
      addVmColumn.vmId = this.vmid;
      Object.assign(dataItem, addVmColumn);
      addVmColumn.vmId = this.vmid;
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList.splice(index, 1);
      this.dipValueMappingService.valueMappingEditModel.vmColumnModelAddList.push(addVmColumn);
      console.log('addVmColumn', addVmColumn);
    } else {
      if (!formGroup.valid) {
        // prevent closing the edited cell if there are invalid values.
        args.preventDefault();
      } else if (formGroup.dirty) {
        const editVmColumn = formGroup.value as DipVmColumnModel;
        Object.assign(dataItem, editVmColumn);
        const editindex = this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList
          .findIndex(vmc => vmc.id === (formGroup.value as DipVmColumnModel).id);
        if (editindex > -1) {
          this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList.splice(editindex, 1);
        }
        this.dipValueMappingService.valueMappingEditModel.vmColumnModelEditList.push(formGroup.value as DipVmColumnModel);
      }
    }
    const num = this.gridData.findIndex(vmc => vmc.id === dataItem.id);
    this.kSrcGrid.data[num] = dataItem;
    this.kSrcGrid.closeCell();
    this.kSrcGrid.cancelCell();
  }

  public createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': dataItem.id,
      'vmId': dataItem.vmId,
      'code': dataItem.code,
      'colText': dataItem.colText,
      'dataType': dataItem.dataType,
      'editMode': dataItem.editMode,
      'helpId': dataItem.helpId,
      'helpCode': dataItem.helpCode,
      'direction': dataItem.direction,
      'helpColumn': dataItem.helpColumn,
      'helpFilter': dataItem.helpFilter,
      'groupFilter': dataItem.groupFilter
    });
  }
}
